import React from 'react'
import {FaInbox,FaRegCalendarAlt,FaRegCalendar} from 'react-icons/fa'
const Sidebar = ({selectedTab,setSelectedTab}) => {
  //console.log(selectedTab);
  return (
    <div className="sidebar">
      <div onClick={()=>setSelectedTab("INBOX")} >Add Tasks <FaInbox className="icon "/></div>
      <div onClick={()=>setSelectedTab("TODAY")} > Today's Tasks<FaRegCalendarAlt className="icon "/></div>
      <div onClick={()=>setSelectedTab("NEXT")}> Future Tasks<FaRegCalendar className="icon "/></div>
    </div>
  )
}

export default Sidebar
