import React,{useState} from 'react'
import DayPickerInput from 'react-day-picker/DayPickerInput';
import "react-day-picker/lib/style.css";
import dateFnsFormat from 'date-fns/format';
import isAfter from 'date-fns/isAfter';
import isBefore from 'date-fns/isBefore';
import addDays from 'date-fns/addDays';
import isToday from 'date-fns/isToday';

const FORMAT="dd/MM/yyyy";
function formatDate(date,format,locale){return dateFnsFormat(date,format,{locale});}

const AddTask=({onCancel,onAddTask})=>{
  const [task,setTask]=useState("");
  const [date,setDate]=useState(null);
  return(

    <div className="add-task-dialog">

      <input value={task} onChange={(e)=>setTask(e.target.value)} />
      <div className="add-task-actions-container">
        <div className="btns-container">
          <button disabled={!task} className="add-btn" onClick={()=>{onAddTask(task,date);onCancel();setTask("");}}>Add</button>
          <button className="cancel-btn" onClick={()=>{onCancel();setTask("")}}>Cancel</button>
        </div>
        <div className="icon-container">
          <DayPickerInput onDayChange={(day)=>setDate(day)} placeholder={`${dateFnsFormat(new Date(),FORMAT)}`} formatDate={formatDate} format={FORMAT} dayPickerProps={{modifiers:{disabled:[{before: new Date()}]}}} />
        </div>
      </div>

    </div>
  );
}

const TASKS_HEADER_MAPPING={
  INBOX:"Add Tasks",
  TODAY:"Today's Tasks",
  NEXT:"Future Tasks"
};

const TaskItems=({selectedTab,tasks})=>{

  if(selectedTab==="NEXT"){return tasks.filter(task=>isAfter(task.date,new Date())&& isBefore(task.date,addDays(new Date(),7))).map(task=><p  id="myTasks">{dateFnsFormat(new Date(task.date),FORMAT)}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{task.text}</p>)}

  if(selectedTab==="TODAY"){return tasks.filter((task)=>isToday(task.date)).map(task=><p  id="myTasks">{dateFnsFormat(new Date(task.date),FORMAT)}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{task.text}</p>)}
return tasks.filter(task=>isAfter(task.date,new Date())&& isBefore(task.date,addDays(new Date(),7))).map(task=><p  id="myTasks">{dateFnsFormat(new Date(task.date),FORMAT)}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{task.text}</p>)
}


const Tasks = ({selectedTab}) => {
  const [showAddTask,setShowAddTask]=useState(false);
  const [tasks,setTasks]=useState([]);
  const addNewTask=(text,date)=>{
    const newTaskItem={text,date:date || new Date()}
setTasks([...tasks,newTaskItem]);
/*setTasks((prevState)=>[...prevState,text]);*/
  }
 /* const clearP=()=>{const x=document.getElementById('clearOnAdd');
  x.style.display='none';
}*/
  return (
    <div className="tasks">
      <h1>{TASKS_HEADER_MAPPING[selectedTab]}</h1>
      {selectedTab==="INBOX" ?(<div className="add-task-btn" onClick={()=>{setShowAddTask((prevState)=>!prevState);}}>
        <span className="plus">‚ûï</span>&nbsp;&nbsp;&nbsp;
        <span className="add-task-text">Add a New Task</span>
      </div>):null}
      {showAddTask && <AddTask onAddTask={addNewTask} onCancel={()=>setShowAddTask(false)} />}
      {tasks.length>0 ?<TaskItems tasks={tasks} selectedTab={selectedTab} />:<p id="clearOnAdd">üëª Woohoo! Nothing to do! üòÅ</p>}
    </div>
  )
}

export default Tasks
