import React from 'react'
import logo from "./assets/images/logo.gif"
const Header = () => {
  return (
    <header className="header" style={{backgroundColor:"pink"}}>

<nav>
  
  <div className="logo">
  <div className="Name"><em>Aaryan Singh Chauhan</em></div>
    <img src={logo} alt="logo placeholder" id="logo-img" />
  </div>
</nav>
    </header>
  )
}

export default Header
